<template>
  <div class="uploader">
    <!--上传成功的图片-->
    <div class="success-images" v-if="successImages != null && successImages.length > 0">
      <!-- <img v-for="(image, index) in successImages" :src="image" :key="index" /> -->
      <p>上传列表</p>
    
        <div class="uploaded-wrapper">
          {{successImages}}
          <div class="upload-item" v-for="image, index in successImages" :key="index">
            <yd-icon name="error-outline" class="deleteIcon" @click.native="DeleteFileFromQiniu(image)"></yd-icon>
            <img :src="'http://' + image" :num="successImages.length" class="uploadedImg" >
            </img>
          </div>
        </div>
    </div>
    
    <!-- 提示错误信息 -->
    <p class="error-color" v-if="error != null">错误信息：{{error}}</p>
    
    <!-- 标题 -->

    <!-- 预览窗口 -->
    <div class="preview" v-if="file.size != null">
        <img id="image" />
        <h3>文件大小：{{file.size / 1024}} KB</h3>
        <h3>文件类型：{{file.type}}</h3>
    </div>
    
    <!-- 上传窗口 -->
    <!-- <input type="checkbox" v-model="autoUpload"> -->
    <div class="upload-btn">
      <input class="upload-img" title="" type="file" accept="image/*" name="file" @change="fileInputOnChange($event)" />
      <img src="../assets/icons/upload.svg" alt="">
      <p>Upload</p>
    </div>
    <button v-if="!autoUpload" @click="uploadButtonOnClick()" :disabled="file.size != null && buttonAvailable ? null :'disabled'">Upload</button>
  </div>
</template>

<script>
import url from '@/api/qiniu-token';
import {APIQiniuDelete} from '@/api/api'
export default {
  data() {
    return {
      error: null,
      // 按钮可用状态
      buttonAvailable: true,
      // 自动上传
      autoUpload: true,
      file: {
        self: null,
        size: null,
        type: null,
        suffix: null,
      },
      // 服务器返回的令牌和一些信息
      serverToken: {
        token: null,
        expire: null,
        key: null,
        url: null,
      },
      successImages: [],
    };
  },
  methods: {
    // 上传按钮被点击
    uploadButtonOnClick() {
      // 临时禁用上传按钮
      this.buttonAvailable = false;
      // 申请 token 成功回调
      const okCallBack = function (data) {
        this.serverToken = data;
        // 上传到七牛
        this.uploadFileToQiniu();
      };
      // 申请 token 失败回调
      const failCallBack = function (message) {
        this.buttonAvailable = true;
        this.error = message;
      };
      // 获取上传 token
      this.applyUploadToken(this.file.suffix, okCallBack.bind(this), failCallBack.bind(this));
    },
    fileInputOnChange(event) {
      const el = event.currentTarget;
      const file = el.files[0];
      // 判断用户是否选择文件
      if (file != null) {
        // 显示预览
        this.showPreview = true;
        this.file = {
          self: file,
          size: file.size,
          type: file.type,
          suffix: file.name.substring((file.name.lastIndexOf('.') + 1), file.name.length).toLowerCase(),
        };
        this.previewImage(file);
        // 判断自动上传
        if (this.autoUpload) {
          this.uploadButtonOnClick();
        }
      } else {
        this.reset();
      }
    },
    // 恢复默认
    reset() {
      this.file = {
        self: null,
        size: null,
        type: null,
        suffix: null,
      };
      this.error = null;
      this.buttonAvailable = true;
    },
    // 预览图片
    previewImage(file) {
      if (window.FileReader) {
        const fileReader = new FileReader();
        fileReader.onloadend = function (e) {
          const image = document.querySelector('.preview #image');
          image.src = e.target.result;
        };
        fileReader.readAsDataURL(file);
      } else {
        console.warn('不支持 FileReader');
      }
    },
    // 申请上传 token
    applyUploadToken(fileSuffixName, successCallBack, errorCallBack) {
      const requestData = {
        suffix: fileSuffixName,
      };
      // 请求api
      this.$http.post(url.APPLY_QINIUUPLOADTOKEN_API, requestData).then((response) => {
        if (response.status === 200) {
          // 回调
          successCallBack(response.data)
          console.log(response.data)
        } else {
          errorCallBack('向服务器请求上传 token 失败');
        }
      }, () => {
        errorCallBack('检查您的网络连接');
      });
    },
    uploadFileToQiniu() {
      if (this.file.self != null) {
        console.log('开始上传文件');
        // 构建表单
        const formData = new FormData();
        formData.append('token', this.serverToken.token);
        formData.append('file', this.file.self);
        formData.append('key', this.serverToken.key);

    
        
        // 上传
        this.axios({
          method: 'post',
          url: url.QINIU_UPLOAD_HTTP_API,
          // 注意：请求头设置（必须！）
          headers: { 'Content-Type': 'multipart/form-data' },
          data: formData,
        }).then((response) => {
          if (response.status === 200) {
            console.log(response);
            this.successImages.push(this.serverToken.url);
            this.reset();
          }
        }, () => {
          this.error = '上传文件到七牛异常';
        });
      } else {
        this.error = '上传文件到七牛异常';
      }
    },

    DeleteFileFromQiniu(image) {

      APIQiniuDelete({
        'image': image
      }).then( res => {
        console.log(res)
        this.successImages.pop(image)
      }).catch( err => { console.log(err) })
    }
  },
};
</script>


<style scoped>
.upload-btn{
display: inline-block;
position: relative;
padding: 20px;
overflow: hidden;
background-color: #fff;
-webkit-box-shadow: 0 2px 6px 0 hsla(205,9%,48%,.14);
box-shadow: 0 2px 6px 0 hsla(205,9%,48%,.14);
border-radius: 5px;

}
.upload-img{

position: absolute;
width: 100%;
height: 100%;
top: 0;
left: 0;
outline: none;
background-color: transparent;
filter:alpha(opacity=0);

-moz-opacity:0;

-khtml-opacity: 0;

opacity: 0;

}
.success-images ul{
  list-style: none;
  font-size: 14px;
}
.success-images ul li p {
  margin: 7px 0px;
}
.uploader .preview {
  margin-bottom: 20px;
}
.preview #image {
  max-width: 500px;
  max-height: 400px;
  overflow: hidden;
}
.uploader .error-color {
  color:red;
}
.uploader button, .uploader input {
  outline: none;
}
.uploader button {
  border-radius: 4px;
  cursor: pointer;
}.upload-item{
  width: 50%;
}
.uploadedImg{
  width: 100%
}
.uploaded-wrapper .deleteIcon{

}
</style>


